import {React, useEffect} from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import { Typography, Stack, Button } from '@mui/material';

const Opportunities = () => {
    return ( <div>
        <div style={{width:'200px', marginLeft:'auto', marginRight:'auto'}}>
<img style={{width:'200px'}} src={logo}></img></div>
        <Navbar></Navbar>
        <div style={{width:'75%', position: 'relative', marginRight:'auto', marginLeft:'auto'}}>
       <Typography variant="h3" sx={{fontFamily: "'Roboto Mono', monospace", position: 'relative', marginLeft:'25%'}}>Příležitosti v Přífuku</Typography>
       <Typography  variant="h5" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: '25px'}}>Jaké možnosti nabízí spolek Přífuk a jak je využít?</Typography>
       <Stack direction="row">
        <div style={{width:'50%', paddingRight: '15px', textAlign: 'justify'}}><Typography variant="h6" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: '25px'}}> Aktivní zapojení</Typography>
        <Typography variant="p" sx={{fontFamily: "'Roboto Mono', monospace", }}>
        Máte vlastní nápad? Chtěli byste zorganizovat studentskou akci většího nebo menšího rozměru? Chybí vám něco v našem kampusu?
        Chtěli byste něco odpřednášet? Máte obecně nějakou vlastní iniciativu, kterou byste chtěli zrealizovat, ale nevíte jak?
        Napište nám co chcete dělat a my Vám pomůžeme. Seženeme vám finance, pomůžeme sehnat prostory, vyřídíme povolení od fakulty a všechny 
        důležité věci, bez kterých se nic organizovat nedá. Lidé ze spolku vám také pomůžou a když všechno dopadne, tak společně
        s Přífukáckým zaštítěním může vzniknout něco velkého a krásného. 

        </Typography>
        </div>
        <div style={{width:'50%', paddingRight: '15px', textAlign: 'justify'}}>
        <Typography variant="h6" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: '25px'}}>Skoro-aktivní zapojení</Typography>
        <Typography variant="p" sx={{fontFamily: "'Roboto Mono', monospace", }}>
        Nemáte konkrétní iniciativu, ale i přes to byste se rádi připojili do spolkové organizace a pomohli s projekty, které spolek
        zaštiťuje? Přidejte se! Nikdy není dost rukou a my budeme rádi, když se připojíte. Najděte si projekt, který vás bude bavit 
        a nabídněte své ruce. Můžete pomoct nějakou konkrétní schopností, kterou jste nabyli v průběhu vašeho studia a nebo zcela obecně
        třeba při stavění, za barem, a td.

        </Typography></div>
       </Stack>
       <Button href="/ideas" variant="contained" sx={{position: 'relative', marginRight: 'auto', 
        width: '100%', zIndex: 2, backgroundColor: 'green', marginTop: '40px', textAlign: 'center',
        borderRadius: '25px', height: '60px', borderStyle: 'solid'}}>Mám skvělý nápad na skvělou akci!</Button>
       
       
       </div>
    </div> );
}
 
export default Opportunities;