import React from 'react';
import Navbar from './Navbar'
import {Paper, Stack, Typography, Button} from '@mui/material';
import logo from '../Media/logo.png'
import backgroundPhoto from '../Media/backgr.jpg'

let Welcome = () => {
return(
<div style={{}}>
<img src={backgroundPhoto} style={{width:'101%', padding: 0, margin: '-10px', position: 'absolute', top: 0, zIndex: -5}}></img>
<div style={{position: 'relative', marginLeft: 'auto', marginRight: 'auto', width: '20%', zIndex: 2, backgroundColor: 'white', marginTop: '180px', textAlign: 'center', borderRadius: '25px'}}>

<img src={logo} style={{maxWidth:'93%', marginTop: '30px'}}></img>
</div>
<Button href="/events" variant="contained" sx={{position: 'relative', marginLeft: '40%', marginRight: 'auto', 
width: '20%', zIndex: 2, backgroundColor: 'green', marginTop: '40px', textAlign: 'center',
 borderRadius: '25px', height: '60px', borderStyle: 'solid'}}>Co je nového?
</Button>


<div style={{position: 'absolute', top: '20px', left: 'auto', width: '100%'}}><Navbar></Navbar></div>
    
</div>
)}

export default Welcome;
